@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;1,300&family=Nunito:wght@200&family=Roboto+Mono:wght@200&family=Roboto:wght@300&family=Shrikhand&family=Square+Peg&display=swap');


.error {
    font-family: 'Montserrat', sans-serif;
    color: #FF6060;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: calc(100vh - 351.8px);
}

.numero {
    font-size: 288px;
    font-weight: 700;
    align-items: center;
    margin-top: 45px;
    margin-bottom: 45px;
}

.oups {
    font-size: 36px;
    font-weight: 500;
    margin-bottom: 75px;
    align-items: center;
    margin-right: 20px;
    margin-left: 20px;
}

@media screen and (max-width: 768px) {

    .error {
        margin: 0 20px;
        /* height: 680px; */
    }

    .numero {
        font-size: 96px;
        
    }

    .oups {
        font-size: 18px;
        text-align: center;
    }
}